import { ref, reactive } from "vue";
import { WEIXIN_SEND_SMS, FIND_PASSWORD_BY_PHONE_V2, FIND_PASSWORD_BY_EMAIL_V2, SEND_EMAIL_CODE_REST_PASSWORD } from "@/api/api";
export default {
  name: "forgotPassword",
  props: {
    isShow: {
      type: Boolean,
      default: false
    },
    showEmail: {
      type: Boolean,
      default: true
    },
    showPhone: {
      type: Boolean,
      default: true
    },
    emailValue: {
      type: String,
      default: ''
    },
    phoneValue: {
      type: String,
      default: ''
    }
  },

  setup(props, ctx) {
    const handleClose = () => {
      ctx.emit('close', false);
    };

    const switchValue = ref(1);

    const handleSwitch = value => {
      switchValue.value = value;
    };

    const submitLoadingStatus = ref(false);
    let checkCodeBtn = reactive({
      text: 'Get Code',
      loading: false,
      disabled: false,
      duration: 10,
      timer: null
    });

    const getCheckCodeTimer = () => {
      if (checkCodeBtn.duration !== 10) {
        checkCodeBtn.disabled = true;
        checkCodeBtn.loading = true;
      } // 清除定时器


      checkCodeBtn.timer && clearInterval(checkCodeBtn.timer); //开启定时器

      checkCodeBtn.timer = setInterval(() => {
        const tmp = checkCodeBtn.duration--;
        checkCodeBtn.text = `${tmp} S`;
        checkCodeBtn.loading = true;
        checkCodeBtn.disabled = true;

        if (tmp <= 0) {
          //清除定时器
          clearInterval(checkCodeBtn.timer);
          checkCodeBtn.duration = 10;
          checkCodeBtn.text = 'Reacquire'; // 设置按钮可以点击

          checkCodeBtn.disabled = false;
          checkCodeBtn.loading = false;
        }

        console.log(checkCodeBtn);
      }, 1000);
    };

    return {
      handleClose,
      switchValue,
      handleSwitch,
      getCheckCodeTimer,
      checkCodeBtn,
      submitLoadingStatus
    };
  },

  data() {
    return {
      formLabelPosition: 'left',
      dialogWidthValue: '50%',
      forgotForm1: {
        email: '',
        email_code: '',
        password: '',
        confirm_password: ''
      },
      forgotRules1: {
        email_code: [{
          required: true,
          message: 'Please fill out your code.',
          trigger: 'blur'
        }],
        email: [{
          type: 'email',
          required: true,
          message: 'Please fill out your email address.',
          trigger: 'blur'
        }],
        password: [{
          required: true,
          message: 'Please enter your password',
          trigger: 'blur'
        }],
        confirm_password: [{
          required: true,
          message: 'Please enter your password again',
          trigger: 'blur'
        }]
      },
      forgotForm2: {
        phone: '',
        phone_code: '',
        password: '',
        confirm_password: ''
      },
      forgotRules2: {
        phone_code: [{
          required: true,
          message: 'Please fill out your code.',
          trigger: 'blur'
        }],
        phone: [{
          required: true,
          message: 'Please fill out your phone #.',
          trigger: 'blur'
        }],
        password: [{
          required: true,
          message: 'Please enter your password',
          trigger: 'blur'
        }],
        confirm_password: [{
          required: true,
          message: 'Please enter your password again',
          trigger: 'blur'
        }]
      }
    };
  },

  mounted() {
    let screenWidth = document.body.clientWidth;

    if (Math.floor(screenWidth) <= 768) {
      this.dialogWidthValue = "90%";
      this.formLabelPosition = 'top';
    }

    if (Math.floor(screenWidth) > 768 && Math.floor(screenWidth) <= 992) {
      this.dialogWidthValue = "90%";
      this.formLabelPosition = 'top';
    }

    if (Math.floor(screenWidth) > 992 && Math.floor(screenWidth) <= 1200) {
      this.dialogWidthValue = "50%";
      this.formLabelPosition = 'left';
    }

    if (Math.floor(screenWidth) > 1200) {
      this.dialogWidthValue = "50%";
      this.formLabelPosition = 'left';
    }

    window.onresize = () => {
      let screenWidth2 = document.body.clientWidth;

      if (Math.floor(screenWidth2) <= 768) {
        this.dialogWidthValue = "90%";
        this.formLabelPosition = 'top';
      }

      if (Math.floor(screenWidth2) > 768 && Math.floor(screenWidth2) <= 992) {
        this.dialogWidthValue = "90%";
        this.formLabelPosition = 'top';
      }

      if (Math.floor(screenWidth2) > 992 && Math.floor(screenWidth2) <= 1200) {
        this.dialogWidthValue = "50%";
        this.formLabelPosition = 'left';
      }

      if (Math.floor(screenWidth2) > 1200) {
        this.dialogWidthValue = "50%";
        this.formLabelPosition = 'left';
      } // console.log(document.body.clientWidth)

    };
  },

  unmounted() {
    window.onresize = null;
  },

  methods: {
    getCheckCode() {
      let phone = this.forgotForm2.phone;

      if (phone) {
        let params = {
          phone: phone
        };
        WEIXIN_SEND_SMS(params).then(res => {
          console.log(res);

          if (res.code === 200) {
            this.getCheckCodeTimer();
            this.$message.success('Success');
          }
        }).catch(err => {
          this.$message.error(err.msg);
        });
      }
    },

    sendEmailCode(formName) {
      let self = this;
      let email = this.forgotForm1.email;
      this.$refs[formName].validateField('email', valid => {
        if (valid) {
          if (email) {
            let params = {
              email: email
            };
            SEND_EMAIL_CODE_REST_PASSWORD(params).then(res => {
              if (res.code == 200) {
                self.getCheckCodeTimer();
                self.$message.success('Success');
              }
            }).catch(err => {
              console.log(err);
              return this.$message.error(err.msg);
            });
          }
        } else {
          console.log('error submit!!');
          return this.$message({
            type: 'warning',
            message: 'Please enter a valid email address',
            grouping: true
          });
        }
      });
    },

    submitForm1(formName) {
      this.submitLoadingStatus = true;
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = Object.assign({}, this.forgotForm1);
          FIND_PASSWORD_BY_EMAIL_V2(params).then(res => {
            console.log(res);

            if (res.code == 200) {
              this.$message.success(res.msg);
              this.submitLoadingStatus = false;
              this.handleClose();
            }
          }).catch(err => {
            console.log(err);
            this.submitLoadingStatus = false;
            this.$message.error(err.msg);
          });
        } else {
          console.log('error submit!!');
          this.submitLoadingStatus = false;
          return false;
        }
      });
    },

    submitForm2(formName) {
      this.submitLoadingStatus = true;
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = Object.assign({}, this.forgotForm2);
          FIND_PASSWORD_BY_PHONE_V2(params).then(res => {
            console.log(res);

            if (res.code == 200) {
              this.$message.success(res.msg);
              this.submitLoadingStatus = false;
              this.handleClose();
            }
          }).catch(err => {
            console.log(err);
            this.submitLoadingStatus = false;
            this.$message.error(err.msg);
          });
        } else {
          console.log('error submit!!');
          this.submitLoadingStatus = false;
          return false;
        }
      });
    }

  }
};